import React, {useState, useEffect} from 'react';

const ProfileStatusWithHooks = (props) => {
	//	[ state, -   state] = useState(  state);
	let [editMode, setEditMode] = useState(false);	//   state   hook - useState
	let [status, setStatus] = useState(props.status);				// useState    2 

	useEffect(() => { //  global state,  ,  local state
		setStatus(props.status);	// local state
	}, [props.status]);					// global state

	const activateEditMode = () => {
		setEditMode(true); // local state
	}

	const deactivateEditMode = () => {
		setEditMode(false);		// local state
		props.updateStatus(status);	// global state (modified with thunk)
	}

	const onStatusChange = (e) => {
		setStatus(e.currentTarget.value); // local state
	}

	return (
		<div>
			{!editMode &&
				<div>{'Status: '}
					<span onDoubleClick={activateEditMode}>
							{props.status || "-------"} {/* global state */}
						</span>
				</div>
			}
			{editMode &&
				<div>{'Status: '}
					<input autoFocus={true}
								 onChange={onStatusChange}
								 onBlur={deactivateEditMode}
								 value={status}/> {/* local state */}
				</div>
			}
		</div>
	)
}

export default ProfileStatusWithHooks;
